		newsList,
		getNewsDetail,
		getCategoryList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	import pagination from '@/components/pagination';
	export default {
		metaInfo:window.$VueMetaInfo['legalCounsel'],
		components: {
			pagination
		},
		data() {
			return {
				totalCount: 100, //
				params: {
					page: 1,
					rows: 6,
					categoryUuid: ''
				},
				newsAllList: [],
				categoryList: [],
				processIndex: 0,
				legalProcessList: [
					{
						title: '',
						content: ''
					},
