<script>
	import {
		getContactUsList,
		newsList,
		getNewsDetail,
		getCategoryList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	import pagination from '@/components/pagination';
	export default {
		metaInfo:window.$VueMetaInfo['criminal'],
		components: {
			pagination
		},
		data() {
			return {
				totalCount: 100, //
				params: {
					page: 1,
					rows: 6,
					categoryUuid: ''
				},
				newsAllList: [],
				categoryList: [],
				enterpriseInfo: {}, //
			}
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				let categoryList = this.$store.state.categoryInfo
				this.params.categoryUuid = categoryList.find((item) => item.categoryName == '').uuid
				this.newsList()
			}
			if (this.$store.state.companyInfo) {
				this.enterpriseInfo = this.$store.state.companyInfo
			} else {
				this.getContactUsList()
			}

		},
		methods: {
			//
			getContactUsList() {
				getContactUsList({}).then(res => {
					if (res.data.success == 'true') {
						this.enterpriseInfo = res.data.data
					}
				});
			},
			//
			newsList() {
				newsList(this.params).then(res => {
					if (res.data.success == 'true') {
						this.newsAllList = res.data.data.data
						this.totalCount = res.data.data.total
						this.newsAllList.forEach(item => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, 'YYYY-mm');
								item.day = UTCUtil.showLocalDate(item.publishTime, 'dd');
							}
						})
					}
				});
			},
			toNewsDetails(uuid) {
				this.$router.push('/news-details/'+uuid)
			},
			//
			toggleNavigation(url) {
				// if(this.$route.path == url){
				// 	this.$router.go(0)
				// }
				this.$router.push({
					path: url,
					query: {

					}
				});
			},
			//
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList()
			},

		}
	}
</script>
