<script>
	import {
		getContactUsList,
		newsList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	import pagination from '@/components/pagination';
	export default {
		components: {
			pagination
		},
		data() {
			return {
				totalCount: 100, //
				newsAllList: [],
				params: {
					page: 1,
					rows: 3,
					categoryUuid: ''
				},
				legalProcessList: [
					{
						title: '',
						content: ''
					},
					{
						title: '',
						content: ''
					},
					{
						title: '',
						content: ''
					},
